.card {
    height: 23rem;
    background-color: black;
    color: white;
    border-radius: 0;
    margin-top: 150px;
}
.card-img-top {
    margin-top: -10vh;
}
#bodyDivider {
    height: 2px;
    width: 15rem;
    border-width: 0;
    color: #fffaff;
    background-color: #fffaff;
}
#company {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    color: #00d5ff;
}
.card-title {
    text-transform: capitalize;
    font-family: 'Lato', sans-serif;
    font-weight: 900;
}
#profile {
    width: 10rem;
    height: 10rem;
    border: 5px solid #fffaff !important;
}
@media (max-width: 768px) {
    .maxw {
        margin-top: 5em !important;
    }

    .card-main-tw {
        margin-bottom: 100px !important;
    }
}
@media (min-width: 768px) {
    .maxw {
        width: 100% !important;
    }
    .card-img-top {
        margin-top: -7vh;
    }
}
@media (min-width: 992px) {
    .maxw {
        width: 100% !important;
    }
    .card-img-top {
        margin-top: -6vh;
    }
}
@media (min-width: 1080px) {
    .maxw {
        max-width: 75% !important;
    }
    .card-img-top {
        margin-top: -10vh;
    }
}

.card-main-tw {
    margin-bottom: 100px !important;
}
